.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: calc(3vh);
}

.App-link {
  color: #61dafb;
}

.App-text {
  font-weight: bolder;
  text-shadow: 10px 10px #000, 10.5px 10.5px #000;
  word-spacing: 100vw;
  text-align: left;
  margin-left: 20vw;
  font-size: calc(10vh);
}

.App-logo {
  height: 5vh;
  margin-left: 15px;
  margin-right: 15px;
  filter: drop-shadow(5px 5px 0px black);
}

body {
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}